/* eslint-env browser */
window['back-to'] = null;
[...document.querySelectorAll('[data-smooth]')].forEach((el) => {
  el.onclick = () => {
    const name = el.getAttribute('href')
    const target = document.querySelector(name)
    if (target) {
      window['back-to'] = { 'y': window.scrollY, name: name.replace('#', '') }
      target.scrollIntoView({ behavior: 'smooth' })
    }
    return false
  }
})